@import "../../../../Global/ScssPartials/systemColorScheme";
@import "../../../../Global/ScssPartials/utilities";

.ProfileDrawer {
  width: 60%;
  flex-shrink: 0;
  height: 100%;
  * {
    font-size: 1em;
  }
}
.DrawerCard {
  padding: 5px;
  // @extend .frameGradient;
  @media only screen and (min-width: 992px) {
    width: 73%;
    align-self: center;
  }
}
.CardStack {
  background-color: white;
  @include flex-center(row, space-between, center);
}
.MenuDrawerSummaryDashBoard {
  img {
    background-color: white;
    width: 75px;
    height: 75px;
    border-radius: 5px;
    padding: 2px;
  }
}

.MenuDrawerSummaryUserImageContainer {
  margin-left: 10px;
  padding: 5px;
  border-radius: 4px;
  // @extend .frameGradient;
  @include flex-center(row, center, center);
  text-align: center;
}
.MenuDrawerSummaryStackOptions {
  padding: 1em;
  padding-right: 0;
  padding-block: 0;
  @include flex-center(column, initial, center, 1em);
}
.MenuDrawerSummaryOptions {
  @include grid-cell();
}
